import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            title: '添加快捷回复',
            content: '',
            Id: null,
            user: this.$store.state.userInfo.Data
        }
    },
    created(){
        if(this.$store.state.reply){
            this.Id = this.$store.state.reply.Id
            this.content = this.$store.state.reply.Content
        }
    },
    methods: {
        add(){
            if(!this.content){
                return this.$toast('请输入内容')
            }
            if(this.Id){
                this.$post('IMAPI/Account/UpdateOftenUseWords',{
                    Id: this.Id,
                    Content: this.content
                }).then(o => {
                    this.$store.dispatch('pushreply', null)
                    this.$router.back()
                })
            }
            else{
                this.$post('IMAPI/Account/AddOftenUseWords',{
                    UserType: 2,
                    UserId: this.user.User.DoctorId,
                    Content: this.content
                }).then(o => {
                    this.$router.back()
                })
            }
        }
    }
}



